import React from 'react'
import SwiperCore, { Virtual, Navigation, Pagination,Autoplay } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import banner1 from "./imgs/banner_1.jpg";
import banner2 from "./imgs/banner_2.jpg";
import banner1mobile from "./imgs/banner_1_mobile.jpg";
import banner2mobile from "./imgs/banner_2_mobile.jpg";
import banner3mobile from "./imgs/Banner_mobile_banner3.jpg";
import { useLocation } from 'react-router-dom';

import banner_3 from "./imgs/banner_3.jpg";
import {useState,setState} from 'react';

SwiperCore.use([Virtual, Navigation, Pagination,Autoplay]);
function TopBanner() {
  const [slideperview, setslideperview] = useState(1);
  const location = useLocation()
  const [windowSize, setWindowSize] = useState(getWindowSize());
  const partylist= 
  {
      servicelist:[
  
        {
          service:"Get In touch",link:banner1,bgcolor:"white",link_mobile:banner1mobile,alt:"hvac near me"
        },
        {
          service:"Future",link:banner2,bgcolor:"#03142e",link_mobile:banner2mobile,alt:"ac repair"
        }
        ,
        {
          service:"OfferBanner",link:banner_3,bgcolor:"#03142e",link_mobile:banner3mobile,alt:"heating and cooling near me"
        }
      ]
    }


    function getWindowSize() {
        const {innerWidth, innerHeight} = window;
        return {innerWidth, innerHeight};
      }
      const slides=[];
      partylist.servicelist.map(serviceobj =>slides.push(<SwiperSlide key={`slide-${serviceobj.service}`}><img src={windowSize.innerWidth>673?serviceobj.link:serviceobj.link_mobile} 
         
          className='desc' alt={serviceobj.alt}></img></SwiperSlide>) 
  
        )
  
  


  return (
    <div>
    {location.pathname=="/index" ? 
      <Swiper
    
    slidesPerView={slideperview}
    centeredSlides={true}
    spaceBetween={5}
    autoplay={{delay:6000,
    disableOnInteraction: false,
    pauseOnMouseEnter: true}}
    direction={'horizontal'}
    modules={[Pagination]}
    pagination={{ clickable: true }}
    scrollbar={{ draggable: true }}
    navigation={true}
    virtual
  >{slides}</Swiper>:null}
  </div>
  )
}

export default TopBanner