import React from 'react'
import whatsapp from "../components/imgs/WhatsApp_icon.png"
import whatsapp_chatwithus from "../components/imgs/wa_chatwithus.png"
import  thc1logo  from "../components/imgs/trlogo2.png";
import sheduleameeting from "../components/imgs/schedule_meeting.png"
import { useState,useEffect } from 'react';
const Logo_whatsapp_meeting = () => {
    const [windowSize, setWindowSize] = useState(getWindowSize());

    function getWindowSize() {
        const {innerWidth, innerHeight} = window;
        return {innerWidth, innerHeight};
      }
    useEffect(()=>{
        function handleWindowResize() {
            setWindowSize(getWindowSize());
          }
       window.addEventListener('resize', handleWindowResize);
      
          return () => {
            window.removeEventListener('resize', handleWindowResize);
          };
    
    
    },[])
  return (
    <div className="whatsapp_meeting_holder">
    <div className="phone_holder meeting_desktop_img_holder">
     <a href="Book_a_meeting.html#/Book_a_meeting" >
       {windowSize.innerWidth>800?
       <img className="whatsapp_img" src={sheduleameeting}/>:
       <img className="whatsapp_img_mobile shcedulemeeting" 
       src={sheduleameeting}/>}
       </a>
     </div>
     <div className="logo_comapny_holder"> 
     <img 
     
       src={thc1logo} className="logoimg"
       /> <h1 className="company_name">Treatedaer</h1>
       </div>
<div className="phone_holder"><a href="https://wa.me/918489792955" target="_blank">{windowSize.innerWidth>800?<img className="whatsapp_img" src={whatsapp_chatwithus}/>:<img className="whatsapp_img_mobile" src={whatsapp}/>}</a></div>

</div>
  )
}

export default Logo_whatsapp_meeting