import "../Assests/css/Quote.css";
import Clock from 'react-live-clock';

import { useEffect,useState } from "react";

const Dailyquote=()=>{
    const [weekdayquote, setWeekdayquote] = useState(null);
    const [datetimelive, setDatetimelive] = useState(null);
    useEffect(() => {
      const weekday = ["A Sunday Well Spent Brings a week of content",
      "Make each day a master place",
       "Work Hard in ilence, Let sucess make the noise",
       "opportunities dont happens, you create them",
       "Stay in your lane, comparison kills creativity",
       "Your Patience is your power",
       "Done is better than perfect"
      ];


var today = new Date();
var dd = String(today.getDate()).padStart(2, '0');
var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
var yyyy = today.getFullYear();
var hour = today.getHours().toString(); // => 9
var minutes = today.getMinutes().toString(); // =>  30
var seconds = today.getSeconds().toString(); 
const currdatetime = dd + '-' + mm + '-' + yyyy ;
setDatetimelive(currdatetime)

setWeekdayquote(weekday[today.getDay()])
}, [])
/*<Clock format={'HH:mm:ss'} ticking={true} timezone={'Asia/Kolkata'} /> */
return(
  
  <blockquote className="blockquote">
   
  <svg viewBox="0 0 512 512" width="80" title="quote-left" className="quote-icon">
    <path d="M464 256h-80v-64c0-35.3 28.7-64 64-64h8c13.3 0 24-10.7 24-24V56c0-13.3-10.7-24-24-24h-8c-88.4 0-160 71.6-160 160v240c0 26.5 21.5 48 48 48h128c26.5 0 48-21.5 48-48V304c0-26.5-21.5-48-48-48zm-288 0H96v-64c0-35.3 28.7-64 64-64h8c13.3 0 24-10.7 24-24V56c0-13.3-10.7-24-24-24h-8C71.6 32 0 103.6 0 192v240c0 26.5 21.5 48 48 48h128c26.5 0 48-21.5 48-48V304c0-26.5-21.5-48-48-48z" />
  </svg>
  <div>
  <p className="quote">{weekdayquote}</p>
  <p className="live-date-time">{datetimelive} | <Clock format={'HH:mm:ss'} ticking={true} timezone={'Asia/Kolkata'} /> </p>
  </div>

</blockquote>
)



   }

   export default Dailyquote;