import "../Assests/css/Service_list.scss";
import Dailyquote from "../components/DailyQuote";
import MiddleNavBar from "../components/MiddleNavBar";
import { useEffect, useState } from "react";
import SocialMedia from "../components/SocialMedia";
import thc1logo from "../components/imgs/trlogo2.png";
import { useLocation} from "react-router-dom";
import ResNav from "../components/ResNav";
import Footer from "../components/Footer";
import Logo_whatsapp_meeting from "../components/Logo_whatsapp_meeting";
function Service_List_Holder({
  bgurl,
  topic,
  desc,
  partylist,
  product_display_holder,
}) {
  const [windowSize, setWindowSize] = useState(getWindowSize());
  function getWindowSize() {
    const { innerWidth, innerHeight } = window;
    return { innerWidth, innerHeight };
  }
  useEffect(() => {
    function handleWindowResize() {
      setWindowSize(getWindowSize());
    }
    window.addEventListener("resize", handleWindowResize);

    return () => {
      window.removeEventListener("resize", handleWindowResize);
    };
  }, []);
  const slides = [];
  partylist.servicelist.map((serviceobj) =>
    slides.push(
      <div
        id={serviceobj.service.replace(/ /g, "_")}
        className="collingservicecontainer"
      >
        <div
          className="collingservice"
          style={{
            background: `linear-gradient(to bottom, rgba(0, 0, 0, 0.3) 0.1%, rgba(0, 0, 0, 0.7)), url('${serviceobj.link}')`,
            backgroundSize: "cover",
            backgroundRepeat: "no-repeat",
            backgroundPositionX: "50%, 50% , 50%,50%",
            backgroundPositionY: "50%, 50%",
          }}
        ></div>
        <div className="service_Details_container">
          <h1>{serviceobj.service}</h1>

          <div className="servicedetails">{serviceobj.desc}</div>
          <div className="tagscontainer">
            {" "}
            {serviceobj.tags.map((tag) => (
              <div className="tagname">{tag}</div>
            ))}
          </div>

          <button
            value={serviceobj.service}
            onClick={(e) =>
              (window.location.href = "#/quoteform?_q=" + e.target.value)
            }
          >
            Get quote
          </button>
        </div>
      </div>
    )
  );
  const navpath = useLocation().pathname;
  const navhash = useLocation().hash;
 useEffect(() => {  

   

     
   
     if(document.getElementById(navhash.slice(1).replace(/_/g,"_"))){
       console.log(navhash.slice(1).replace(/_/g,"_"))
       document.getElementById(navhash.slice(1).replace(/_/g,"_")).scrollIntoView()
      
     }  
 else{
   document.body.scrollTop = 0; // For Safari
   document.documentElement.scrollTop = 0
 }
         



        },[]);
  return (
    <div className="wholebodycontainer">
      <div>
    
        <ResNav/>
   
      </div>
      <div className="Header">
        <div class="social-quote">
          <SocialMedia />
          <Dailyquote />
        </div>
        <MiddleNavBar />
     
      </div>
   <Logo_whatsapp_meeting/>
      <center>
        <h1 class="ventilation_txt">{topic}</h1>
      </center>
      <div className="bgcover">
        <div class="bg" style={
          { color:"white",
          backgroundImage: "url('"+bgurl+ "')",
        }
            } ></div>
            </div>
      <div class="ventdis">{desc}</div>

      <div className="product_List_Holder_Where_holder">
        <div className="product_List_Holder">
        {product_display_holder.product_list.map((products, i) => (
          <div className="product_display_holder">
            {products.imglink.length == 0 ? null : (
              <img width={250} src={products.imglink}></img>
            )}
            <div
              className={
                products.imglink.length == 0
                  ? "products_holder noimgstyle"
                  : "products_holder"
              }
            >
              <h2>
                {" "}
                {products.main_topic_link == 0 ? (
                  products.topic
                ) : (
                  <a href={"/" + products.main_topic_link}>{products.topic}</a>
                )}
              </h2>
              {products.details.map((details_point) => (
                <div className="product_name">{details_point}</div>
              ))}
              <button
                value={products.topic}
                onClick={(e) =>
                  (window.location.href =
                    "quoteform.html#/quoteform?_q=" + e.target.value)
                }
              >
                Get Quote
              </button>
            </div>
          </div>
        ))}

</div>
        
        <div className="collingsystem">
        <center>
        <h2 className="topic">Where?</h2>
       </center>
        
          <div className="collingsystemholder">{slides}</div>
        </div>
      </div>
      <Footer/>
    </div>
  );
}

export default Service_List_Holder;
