import React from 'react'
import "../Assests/css/MiddleNavbar.scss"

const servicelistnavbarmain = [
    {
      values: [
      ],
      name: "Home",
      link: "index",
    },
    {
      values: [
        "Car parking Area",
        "Basement",
        "Electrical Panel Room",
        "Restroom",
      ],
      name: "Ventilation",
      link: "Ventilation",
    },
    {
      values: [
        "Hospitals",
        "Residence",
        "Shopping Malls",
        "Server Area",
        "Pharmacy",
      ],
  
      name: "Cooling",
      link: "Cooling",
    },
    {
      values: [
        "Commisioning works to be completed",
        "Annual Maintenance Service",
        "Labour Maintenance service ",
      ],
      imglink:"",
      name: "Service and Maintenance",
      link: "Maintaince_Repair",
    },
  ];

const MiddleNavBar = () => {
  

  return (
    <div className="navitemsholder">

        
    {servicelistnavbarmain.map((item2)=>(
      <div className="item">
       <div className="item_a_conatiner"> <a href={item2.link+".html"+"#/"+item2.link}>{item2.name}</a></div>
      <div className="navitemsection">
      {
      item2.values.map((item) => (
      <div className="navitem_subsection">
          
          <a item={item.replace(/ /g,"_")} onClick={(e) => document.getElementById(e.target.getAttribute("item")).scrollIntoView() } href={item2.link+".html"+"#/"+item2.link + "#" + item.replace(/\s/g,"_")}>
            {item}
          </a>
     
      </div>
    )
    
    
    )}
     
    </div>
    </div>
    )
    )
    
    
    }

    </div>
  )
}

export default MiddleNavBar;
